
.address-form-main {
  input {
    border-radius: 0;
    border-top: none;
    border-right: none;
    border-left: none;
    padding: 15px;
    height: 50px;
    border-bottom: 2px solid var(--dark-gray);
    color: var(--dark-gray);

    &::placeholder {
      color: #000;
    }
  }

  .add-address-btn {
    width: 240px;
    height: 50px;
    border-radius: 10px;
    box-shadow: 0 10px 25px #FCAE2A33 !important;
  }
}
